import './style/main.css'
import * as THREE from 'three'
import * as dat from 'dat.gui'
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls"
import vertexShader from "./shader/vertexShader.glsl"
import fragmentShader from "./shader/fragmentShader.glsl"



//Gui
const gui = new dat.GUI({ width: 300})
gui.close()
const debugObject = {}

let guiTap = document.querySelector('.close-button')
console.log(guiTap)
guiTap.style.background = '#d15858'


debugObject.deepColor = "#c0bdbd"
debugObject.lightColor = "#ff7979"

/**
 * Sizes
 */
const sizes = {}
sizes.width = window.innerWidth
sizes.height = window.innerHeight

window.addEventListener('resize', () =>
{
    // Save sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
})

/**
 * Environnements
 */
// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color('#ff7979');
scene.fog = new THREE.Fog('#ff7979',2, 5)

// Camera
const camera = new THREE.PerspectiveCamera(100, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0, 1, 2)
scene.add(camera)
camera.lookAt(new THREE.Vector3(0,0,0))

// Object
const geometry = new THREE.PlaneBufferGeometry(8,8,256,256)


const material = new THREE.ShaderMaterial({
    wireframe: false,
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
    side: THREE.DoubleSide,


    uniforms: {
        uTime: {value: 0.0},

        uBigWavesElevation: { value: 0.2},
        uBigWavesFrequency: {value: new THREE.Vector2(4.0,1.5)},
        uBigWavesSpeed: { value: 0.7},


        //Periln noise
        uSmallWavesFrequency: {value: 3.0},
        uSmallWavesIteration: {value: 2.0},
        uSmallWavesSpeed: {value: 0.2},
        uSmallWavesElevation: {value: 0.15},


        uDeepColor: {value: new THREE.Color(debugObject.deepColor)},
        uLightColor: {value: new THREE.Color(debugObject.lightColor)},

        uColorOffset: {value: 0.08},
        uColorMultiplier: {value: 5.0},

        //This is how fog works
        fogColor:    { type: "c", value: scene.fog.color },
        fogNear:     { type: "f", value: scene.fog.near },
        fogFar:      { type: "f", value: scene.fog.far }

        },
        fog: true

})


gui.add(material.uniforms.uBigWavesElevation, "value")
    .min(0.0)
    .max(1.0)
    .name("uBigWavesElevation")
    .step(0.001)
gui.add(material.uniforms.uBigWavesFrequency.value, "x")
    .min(0.0)
    .max(10.0)
    .name("uBigWavesFrequencyX")
    .step(0.001)
    gui.add(material.uniforms.uBigWavesFrequency.value, "y")
    .min(0.0)
    .max(10.0)
    .name("uBigWavesFrequencyZ")
    .step(0.001)
gui.add(material.uniforms.uBigWavesSpeed, "value")
    .min(0.1)
    .max(3.0)
    .name("waveSpeed")
    .step(0.001)

gui.add(material.uniforms.uColorMultiplier, "value")
    .min(0.0)
    .max(10.0)
    .step(0.001)
    .name("uColorMultiplier")

    gui.add(material.uniforms.uColorOffset, "value")
    .min(0.0)
    .max(1.0)
    .step(0.001)
    .name("uColorOffset")

gui.addColor(debugObject, "deepColor")
    .name("DeepColor")
    .onChange(()=>{
        //Don't just assign value to deepColor. cuz it's instance of Three class
        material.uniforms.uDeepColor.value.set(debugObject.deepColor)
    })

gui.addColor(debugObject, "lightColor")
    .name("lightColor")
    .onChange(()=>{
        //Don't just assign value to deepColor. cuz it's instance of Three class
        material.uniforms.uLightColor.value.set(debugObject.lightColor)
    })


gui.add(material.uniforms.uSmallWavesFrequency, "value")
    .min(0.0)
    .max(10.0)
    .step(0.001)
    .name("uSmallWavesFrequency")


// gui.add(material.uniforms.uSmallWavesIteration, "value")
//     .min(0.0)
//     .max(10.0)
//     .step(1)
//     .name("uSmallWavesIteration")


gui.add(material.uniforms.uSmallWavesSpeed, "value")
    .min(0.0)
    .max(5.0)
    .step(0.001)
    .name("uSmallWavesSpeed")

gui.add(material.uniforms.uSmallWavesElevation, "value")
    .min(0.0)
    .max(5.0)
    .step(0.001)
    .name("uSmallWavesElevation")





const plane = new THREE.Mesh(
    geometry,
    material
)
scene.add(plane)
plane.rotation.x = -Math.PI * 0.5



const sun = new THREE.Mesh(
    new THREE.SphereGeometry(0.2,32,32),
    new THREE.MeshStandardMaterial({
        color: 'white',
        emissive: 0xfc2424
        
    })
)
sun.position.y = -0.5
scene.add(sun)


const light = new THREE.DirectionalLight()
scene.add(light)
light.position.set(0, 4, -4)



// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: document.querySelector('.webgl')
})
renderer.setPixelRatio(window.devicePixelRatio)
renderer.setSize(sizes.width, sizes.height)

// control
const control = new OrbitControls(camera, document.querySelector('.webgl'))
control.update()
control.enableDamping = true



// function aplicarZoomCamera() {
    
//     if (camera.fov === 10) {
// 		camera.fov = 75;
// 	}
// 	camera.fov -= 0.5;
// 	camera.updateProjectionMatrix();
// }


//Camera Moving

function aplicarZoomCamera() {
    
    
    if(camera.fov <= 100 && camera.fov > 40){
    camera.fov -= 0.5

    for(let i = 0; i < 20; i++){
        camera.position.x -= 0.001
    }

    for(let i = 0; i < 8; i++){
        sun.position.y += 0.001
    }

    camera.updateProjectionMatrix()    
    }

}


/**
 * Loop
 */
const clock = new THREE.Clock()

const loop = () =>
{

    const elapsedTime = clock.getElapsedTime()

    // console.log(elapsedTime)


    //update Water
    material.uniforms.uTime.value = elapsedTime

    // Update


    control.update()
    // camera.lookAt(new THREE.Vector3(0,0,0))


    //Camera moving

    aplicarZoomCamera()





    // Render
    renderer.render(scene, camera)

    // Keep looping
    window.requestAnimationFrame(loop)



}
loop()